import Modal from '@components/Modal';
import useInput from '@hooks/useInput';
import { Button, Input, Label } from '@pages/member/SignUp/styles';
import React, { FC, useCallback } from 'react';
import { useParams } from 'react-router';
import { toast } from 'react-toastify';
import useCustomWorkspace from '@hooks/useCustomWorkspace';

interface Props {
  show: boolean;
  onCloseModal: () => void;
  setShowInviteWorkspaceModal: (flag: boolean) => void;
}
const InviteWorkspaceModal: FC<Props> = ({ show, onCloseModal, setShowInviteWorkspaceModal }) => {
  const { url } = useParams<{ url: string; channel: string }>();
  const [newMember, onChangeNewMember, setNewMember] = useInput<string>('');
  const { postAddWorkspaceMemberHook } = useCustomWorkspace();

  const onInviteMember = useCallback(
    async (e: React.FormEvent<HTMLFormElement>) => {
      e.preventDefault();
      if (!newMember || !newMember.trim() || !url) {
        return;
      }

      try {
        await postAddWorkspaceMemberHook({ url, newMember });
        setShowInviteWorkspaceModal(false);
        setNewMember('');
        toast.dismiss();
        toast.success(`Member invitation was successful`, {
          closeOnClick: true,
        });
      } catch (error) {
        toast.error('Failed to invite the member', { position: 'bottom-center' });
      }
    },
    [newMember, postAddWorkspaceMemberHook, setNewMember, setShowInviteWorkspaceModal, url],
  );

  return (
    <Modal show={show} onCloseModal={onCloseModal}>
      <form onSubmit={onInviteMember}>
        <Label id="member-label">
          <span>email</span>
          <Input id="member" type="email" value={newMember} onChange={onChangeNewMember} />
        </Label>
        <Button type="submit">Invite</Button>
      </form>
    </Modal>
  );
};

export default InviteWorkspaceModal;
