import React, { useState } from 'react';
import { toast } from 'react-toastify';
import useCustomWorkspace from '@hooks/useCustomWorkspace';
import { useParams } from 'react-router';
import {
  DisplayText,
  EditIcon,
  EditorContainer,
  StyledButton,
  StyledInput,
} from '@components/WorkspaceNameEditor/styles';

const WorkspaceNameEditor = ({ workspaceName }: { workspaceName: string }) => {
  const { url } = useParams<{ url: string }>();
  const { putWorkspaceNameAsyncHook } = useCustomWorkspace();

  const [isEditing, setIsEditing] = useState(false);
  const [name, setName] = useState(workspaceName);

  const handleEditClick = () => {
    setName(workspaceName);
    setIsEditing(true);
  };

  const handleNameChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setName(e.target.value);
  };

  const handleSaveClick = async () => {
    try {
      if (url) {
        await putWorkspaceNameAsyncHook(url, name);
        toast.dismiss();
        toast.success('Workspace name updated successfully.');
        setIsEditing(false);
      }
    } catch (error) {
      toast.error('Failed to update workspace name.');
    }
  };

  const handleCancelClick = () => {
    setIsEditing(false);
    setName(workspaceName);
  };

  return (
    <EditorContainer>
      {isEditing ? (
        <>
          <StyledInput type="text" value={name} onChange={handleNameChange} />
          <StyledButton onClick={handleSaveClick} color="green">
            <i className="fa fa-check"></i>
          </StyledButton>
          <StyledButton onClick={handleCancelClick} color="red">
            <i className="fa fa-times"></i>
          </StyledButton>
        </>
      ) : (
        <DisplayText onClick={handleEditClick}>
          {workspaceName}
          <EditIcon className="fa fa-pencil" />
        </DisplayText>
      )}
    </EditorContainer>
  );
};

export default WorkspaceNameEditor;
